:root {
  --body-background-light: #fff;
  --body-background-dark: rgb(42, 42, 42);
  --body-background-default: var(--body-background-light);
  --svg-color-light: #000000;
  --svg-color-dark: #ffffff;
  --svg-color-default: var(--svg-color-light);
  --focus-background-light: #c6c8ca;
  --focus-background-dark: transparent;
  --focus-background-default: var(--focus-background-light);
  --font-color-light: #000000;
  --font-color-dark: #ffffff;
  --font-color-default: var(--font-color-light);
  --border-color-light: rgba(0, 0, 0, 0.5);
  --border-color-dark: rgba(255, 255, 255, 0.5);
  --border-color-default: var(--border-color-light);
}

.keyboard {
  color: var(--font-color-default);
  transition: color ease-in 0.3s;
  margin-top: 5px;
  display: flex;
  flex-direction: column;
}

.line {
  display: flex;
  justify-content: center;
}

.key {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  width: 49px;
  height: 49px;
  margin: 1px;
  border: 1px solid var(--border-color-default);
  border-radius: 5px;
  transition: background-color 0.1s ease-in-out, border-color ease-in 0.3s;
  transition-delay: 0s;
  text-transform: uppercase;
  font-size: 16px;
}

.active {
  background: #afafaf;
  transition-duration: 0s;
  transition: background-color 0.1s ease-in;
}

.sup {
  margin-left: 5px;
}

.sys {
  text-transform: none;
  font-size: 12px;
}

.back {
  width: 75px;
}

.tab {
  width: 75px;
}

.caps {
  width: 88px;
}

.shift {
  width: 114px;
}

.enter {
  width: 88px;
}

.space {
  width: 300px;
}

.colored.key {
  color: black;
  transition: color 0.3s ease-in, background-color 0.3s ease-in;
  border: 1px solid rgba(0, 0, 0, 0.1);
}
.colored.violet {
  background-color: #ad7fa8;
}
.colored.blue {
  background-color: #729fcf;
}
.colored.green {
  background-color: #73d216;
}
.colored.orange {
  background-color: #fcaf3e;
}
.colored.yellow {
  background-color: #fce94f;
}
.colored.sys {
  background-color: #dddddd;
}
.colored.violet.active, .colored.blue.active, .colored.green.active, .colored.orange.active, .colored.yellow.active, .colored.sys.active {
  background: rgb(255, 255, 255);
  transition-duration: 0s;
  transition: background-color 0.1s ease-in;
}


